/* montserrat-300 - cyrillic_latin */
@font-face {
  font-family: 'MontserratLight';
  font-style: normal;
  font-weight: normal;
  src: local('Montserrat Light'), local('Montserrat-Light'),
    url('../assets/fonts/MontserratLight/montserrat-v14-cyrillic_latin-300.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('../assets/fonts/MontserratLight/montserrat-v14-cyrillic_latin-300.woff')
      format('woff'),
    /* Modern Browsers */
      url('../assets/fonts/MontserratLight/montserrat-v14-cyrillic_latin-300.ttf')
      format('truetype'); /* Safari, Android, iOS */
}

